import { createStore } from "redux"

const pageConfigs = {
  "e621": {
    "name": "e621",
    "display": "e621",
    "tags": ["furry", "nsfw"],
    "url": {
      "base": "https://e621.net",
      "post": "https://e621.net/posts.json",
      "params": {
        "tags": "tags",
        "page": "page",
        "limit": "limit"
      }
    },
    "posts": ["posts"],
    "file": {
      "post": ["id"],
      "preview": ["preview", "url"],
      "full": ["file", "url"]
    }
  },
  "danbooru": {
    "name": "danbooru",
    "display": "Danbooru",
    "tags": ["anime", "nsfw"],
    "url": {
      "base": "https://danbooru.donmai.us",
      "post": "https://danbooru.donmai.us/posts.json",
      "params": {
        "tags": "tags",
        "page": "page",
        "limit": "limit"
      }
    },
    "posts": [],
    "file": {
      "post": ["id"],
      "preview": ["preview_file_url"],
      "full": ["file_url"]
    }
  },
}

const initialState = {
  posts: [],
  config: "e621"
}

function modifyStore(state = initialState, action) {
  switch (action.type) {
    case "config/modify":
      return { ...state, config: action.payload }
    case "posts/set":
      return { ...state, posts: action.payload }
    default:
      return state
  }
}

const store = createStore(modifyStore)

export { pageConfigs, store }