const getFromPath = (path, resp) => {
  let data = resp

  for (let i = 0; i < path.length; i++) {
    data = data[path[i]]
  }

  return data
}

const determineFileType = (file) => {
  let path = file.split(".")
  let ext = path[path.length - 1]

  let post_type = "none"

  if (["mp4", "webm"].includes(ext)) {
    post_type = "video"
  } else if (ext === "gif") {
    post_type = "gif"
  } else if (ext === "swf") {
    post_type = "flash"
  } else {
    post_type = "image"
  }

  return post_type
}

const capitalizeString = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1)
}

export { getFromPath, determineFileType, capitalizeString }