import React from "react"
import { useCookies } from "react-cookie"
import { useDispatch, useSelector } from "react-redux"

const SiteCard = (props) => {
  const dispatch = useDispatch()
  const state = useSelector(store => store.config)

  const [cookies, setCookie] = useCookies(["site"])

  if (cookies.site !== undefined && cookies.site !== state) {
    dispatch({ type: "config/modify", payload: props.name })
  }

  const handleClick = e => {
    dispatch({ type: "config/modify", payload: props.name })
    setCookie("site", props.name, { path: "/" })
  }

  return (
    <div className="card" onClick={handleClick}>
      <div className="card-body">
        {
          state === props.name
            ? <h2><strong>{props.config.display}</strong> (selected)</h2>
            : <h2>{props.config.display}</h2>
        }
        <p>{props.config.tags.join(", ")}</p>
      </div>
    </div>
  )
}

export default SiteCard