import React, { useState } from "react"
import { useSelector } from "react-redux"
import { getFromPath, determineFileType } from "../Helpers"
import { pageConfigs } from "../Store"

const Post = (props) => {
  const config = useSelector(store => store.config)
  
  let current_config = pageConfigs[config]
  let full = getFromPath(current_config.file.full, props.post)

  const [state, setState] = useState(full ? {
    error: false,
    post: getFromPath(current_config.file.post, props.post),
    full: full,
    preview: getFromPath(current_config.file.preview, props.post),
    type: determineFileType(full),
    displayFull: false
  } : {
    error: true
  })

  if (state.error) {
    return null
  }

  const mouseDownHandler = (event) => {
    if (event.button === 1) {
      const new_window = window.open(state.full)

      if (new_window) {
        new_window.opener = null
      }
    }
  }

  const switchQuality = () => {
    if (state.type === "image" || state.type === "gif") {
      if (state.displayFull) {
        const new_window = window.open(state.full)

        if (new_window) {
          new_window.opener = null
        }
      } else {
        setState({
          ...state,
          displayFull: true
        })
      }
    } else {
      const new_window = window.open(state.full, "_blank", "noopener,noreferrer")

      if (new_window) {
        new_window.opener = null
      }
    }
  }

  return (
    <div className="post">
      <img
        src={state.displayFull ? state.full : state.preview}
        className={state.type}
        onClick={switchQuality}
        onMouseDown={mouseDownHandler}
      />
    </div>
  )
}

export default Post