import "bootstrap/dist/css/bootstrap.min.css"
import "./css/style.css"

import Main from "./Main"
import { store } from "./Store"

ReactDOM.render(
  <Provider store={store}>
    <Main />
  </Provider>,
  document.getElementById("root")
)