import React, { useState } from 'react'
import Post from './Post'
import Search from './Search'
import { useDispatch, useSelector } from "react-redux"

import { pageConfigs } from "../Store"
import { getFromPath } from "../Helpers"

const Posts = (props) => {
  const dispatch = useDispatch()
  const [state, setState] = useState({
    error: null,
    hasSearched: false,
    isLoaded: false
  })

  const items = useSelector(store => store.posts)
  const config = useSelector(store => store.config)

  const doSearch = e => {
    e.preventDefault()

    setState({
      hasSearched: true,
      isLoaded: false
    })

    let current_config = pageConfigs[config]

    fetch(current_config.url.post + "?" + new URLSearchParams({
      tags: e.target.tags.value,
      page: e.target.page.value,
      limit: e.target.limit.value
    }))
    .then(res => res.json())
    .then(
      (result) => {
        setState({
          isLoaded: true,
          error: false
        })

        dispatch({ type: "posts/set", payload: getFromPath(current_config.posts, result)})
      },
      (error) => {
        setState({
          isLoaded: true,
          error: true
        })

        dispatch({ type: "posts/set", payload: [] })
      }
    )
  }

  return (
    <div>
      <Search doSearch={doSearch}/>
      {state.error &&
        <div className="error">
          <p>
            Error: {state.error.message}
          </p>
        </div>
      }
      {!state.isLoaded && state.hasSearched &&
        <div>
          <p className="loading">
            Loading...
          </p>
        </div>
      }
      {state.isLoaded &&
        <div id="posts">
          {items.map(item => (
            <Post post={item}/>
          ))}
        </div>
      }
      {(state.isLoaded && items.length < 1 && !state.error) &&
        <div className="error">
          No posts found.
        </div>
      }
    </div>
  )
}

export default Posts