import React from "react"

import { programConfig } from "../Config"
import { capitalizeString } from "../Helpers"

const authorLink = (service, user) => {
  let url = null

  switch (service) {
    case "github":
      url = "https://github.com/" + user
      break
    default:
      break
  }

  return (
    <span>
      <strong>
        <a href={url}>@{user}</a>
      </strong> on <strong>
        {capitalizeString(service)}
      </strong>
    </span>
  )
}

const About = () => {
  const author = programConfig.author.github

  return (
    <div className="container mt-5">
      <h1>About</h1>
      <p>
        <strong>{programConfig.name}</strong> is a simple, easy to use alternative
        to most image gallery's UI. Made using React by {authorLink("github", author)}.
      </p>
      <h3>v0.1.0</h3>
      <ul>
        <li>Initial release</li>
      </ul>
    </div>
  )
}

export default About