import React from "react"

import { pageConfigs } from "../Store"
import SiteCard from "../components/SiteCard"

const Sites = () => {
  return (
    <div className="container">
      <h1>Sites</h1>
      <p>Choose which site to get posts from.</p>
      {Object.keys(pageConfigs).map((k, i) =>
        <SiteCard key={k} name={k} config={pageConfigs[k]}/>
      )}
    </div>
  )
}

export default Sites